import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { LessonService } from '../../../shared/services/lesson.service';
import { BrowserStorageService } from '../../../shared/services/browser-storage.service';

@Component({
  selector: 'app-reading',
  templateUrl: './reading.component.html',
  styleUrls: ['./reading.component.scss'],
  providers: [ LessonService ],
  encapsulation: ViewEncapsulation.None,
})
export class ReadingComponent implements OnInit {
  
  reading: any;
  content: string;
  example: string;
  id: number;
  background: string;
  standards: string;
  resources: string;

  constructor(private route: ActivatedRoute, private lessonService: LessonService, private browserStorage: BrowserStorageService) { }

  ngOnInit(): void {
    this.route.params.subscribe(params => { 
      this.lessonService.getLessonData(params.id).subscribe(data => {
        this.reading = data.lesson.reading;
        this.content = this.reading.content;
        this.id = data.info.id;
        this.background = data.lesson.reading.background;
        this.standards = data.lesson.reading.standards;
        this.resources = data.lesson.reading.resources;
       });
    });
  }

}
